<template>
  <view>
    <view v-if="showLevelIcon" class="icon-level u-level" :style="[getIcon('color', level)]">
      <uv-icon :name="getIcon('url', level)" size="22"></uv-icon>
      <text>{{ getIcon('label', level) }}</text>
    </view>
    <view v-else class="empty">暂无级别</view>
  </view>
</template>

<script>
export default {
  name: 'LevelIcon',
  props: {
    level: {
      type: [Number, String],
      default: '',
    },
  },
  data() {
    return {}
  },
  computed: {
    showLevelIcon() {
      return ['0', '1', '2', '3', '4'].includes(String(this.level))
    },
  },
  methods: {
    getIcon(type, value) {
      return this.$dict.getDictLabel('promoteLevelList', value, { labelKey: type })
    },
  },
}
</script>

